import React from 'react';
import CustomSelect from '../utilities/CustomSelect';
import { Link } from 'react-router-dom';
import InputTags from '../utilities/InputTags';

export default function CreateService() {
    return (
        <div className="px-2 px-sm-4 px-xxl-5 pb-4 pb-xxl-5">
            <nav className="d-block d-lg-none mt-3 mb-3 mb-sm-5" aria-label="breadcrumb">
                <Link to="/personal-account" className="gray-3">&#10094; </Link>
            </nav>
            <h4 className="text-center color-1 mb-3 mb-sm-4 mb-xl-5"> </h4>
            <form>
                <div className="row align-items-center mb-3 mb-sm-4 mb-xl-5">
                    <div className="col-sm-4">
                        <div className="fs-11 mb-1"> :</div>
                    </div>
                    <div className="col-sm-8">
                        <CustomSelect className="w-100 fs-11" btnClass="inp" checkedOpt="" options={['', '', '', ' ']}/>
                    </div>
                </div>
                <div className="row align-items-center mb-3 mb-sm-4 mb-xl-5">
                    <div className="col-sm-4">
                        <div className="fs-11 mb-1"> :</div>
                    </div>
                    <div className="col-sm-8">
                        <CustomSelect className="w-100 fs-11" btnClass="inp" checkedOpt=" 1 " options={[' 1 ', ' 1 ', ' 3 ', ' 5 ']}/>
                    </div>
                </div>
                <div className="row mb-3 mb-sm-4 mb-xl-5">
                    <div className="col-sm-4">
                        <div className="fs-11 mb-1"> :</div>
                    </div>
                    <div className="col-sm-8">
                        <InputTags name="services" placeholder="   1" />
                    </div>
                </div>
                <div className="row mb-3 mb-sm-4 mb-xl-5">
                    <div className="col-sm-4">
                        <div className="fs-11 mb-1"> :</div>
                    </div>
                    <div className="col-sm-8">
                        <textarea placeholder=" " rows="5" className="fs-11"></textarea>
                    </div>
                </div>
                <div className="row justify-content-end">
                    <div className="col-sm-8">
                        <div className="row row-cols-2">
                            <div><Link to="/personal-account/my-services" className="btn btn-2 w-100 fs-11"></Link></div>
                            <div><button type="submit" className="btn btn-1 w-100 fs-11"></button></div>
                        </div>
                    </div>
                </div>
            </form>
        </div>
    )
}
