import React from 'react';
import { NavLink, Link } from 'react-router-dom';

export default function AccountMenu() {
    return (
        <div>
            <nav className="d-block d-lg-none mt-3 mb-4 mb-sm-5" aria-label="breadcrumb">
                <Link to="/" className="gray-3">&#10094;  </Link>
            </nav>
            <h1 className="d-block d-lg-none text-center color-1 mb-4"> </h1>
            <nav className="menu">
                <ul>
                    <li>
                        <img src="/real_estate/img/icons/pa-1.svg" alt=""/>
                        <NavLink to="profile"></NavLink>
                    </li>
                    <li>
                        <img src="/real_estate/img/icons/pa-2.svg" alt=" "/>
                        <NavLink to="my-ads"> </NavLink>
                    </li>
                    <li>
                        <img src="/real_estate/img/icons/pa-3.svg" alt=" "/>
                        <NavLink to="my-services"> </NavLink>
                    </li>
                    <li>
                        <img src="/real_estate/img/icons/pa-4.svg" alt=""/>
                        <NavLink to="favorites"></NavLink>
                    </li>
                    <li>
                        <img src="/real_estate/img/icons/pa-5.svg" alt=""/>
                        <NavLink to="my-messages"></NavLink>
                        <div className="count">2</div>
                    </li>
                    <li>
                        <img src="/real_estate/img/icons/pa-6.svg" alt=""/>
                        <NavLink to="my-reviews"></NavLink>
                    </li>
                    <li>
                        <img src="/real_estate/img/icons/pa-7.svg" alt=""/>
                        <button type="button"></button>
                    </li>
                </ul>
            </nav>
        </div>
    )
}
